<template>
    <div class="archive-list">
        <div class="md" v-for="(md, index) in mdList" :key="index" @click="test(md)">
            {{md}}
        </div>
        <h2>hahaha</h2>
    </div>
</template>

<script>
import requestApi from '../api/requestApi'

export default {
    name: 'Archive',
    data() {
        return {
            mdList: [],
        }
    },
    methods: {
        test(md) {
            this.$router.push({
                path: '/blog',
                query: {
                    md
                }
            })
        }
    },
    mounted() {
        requestApi.fetch('blogMd').then(res => {
            this.mdList = res.data.map(m => m.substring(0, m.length - 3))
        })
    }
}
</script>

<style lang="less">
.archive-list {
    text-align: center;
}
</style>