import VueRouter from "vue-router"
import Archive from '../components/Archive'
import Blogshow from '../components/Blogshow'


const router = new VueRouter({
    routes:[
        {
            path: '/archive',
            component: Archive,
            meta: {
                title: '归档 | MobbaI'
            }
        },
        {
            path: '/blog',
            component: Blogshow,
            meta: {
                title: '归档 | MobbaI'
            },
            props($route) {
                return {
                    md: $route.query.md
                }
            }
        }
    ]
})

router.afterEach((to) => {
    document.title = to.meta.title || 'Y'
});

export default router