<template>
  <div class="main">
    <div class="blogshow">
      <mavon-editor
        class="markdown"
        v-model="test"
        :subfield="settingMd.subfield"
        :defaultOpen="settingMd.defaultOpen"
        :toolbarsFlag="settingMd.toolbarsFlag"
        :boxShadow="settingMd.boxShadow"
        :previewBackground="settingMd.previewBackground"
      />
    </div>
  </div>
</template>

<script>
import requestApi from '../api/requestApi'

export default {
  name: "Bolgshow",
  data() {
    return {
      settingMd: {
        subfield: false, // 单双栏模式
        defaultOpen: "preview", // 默认展示
        toolbarsFlag: false,
        boxShadow: false,
        previewBackground: "#fff",
        navigation: false, // 默认展示目录
        toolbars: {},
      },
      test: ''
    };
  },
  props: ['md'],
  mounted() {
    requestApi.fetch('blogMd/攒机杂谈.md').then((res) => {
      this.test = res.data;
      console.log('blogMd/攒机杂谈.md')
    });
  },
};
</script>

